@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400;500;700&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  background-image: linear-gradient(#c5bdc5, #755d90);
  background-size: cover;
  display: flex;
  justify-content: center;
  height: 100vh;
}

.form-container {
  align-items: center;
  display: flex;
  justify-content: center;
  margin: 2.5rem 0 0;
  text-align: left;
}

.form-container > * > * {
  margin: 0 0.5rem !important;
}

* {
  font-family: "Poppins", sans-serif;
}

.weather-container {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: 5rem;
}

.weather-sub-container {
  background-color: rgba(0, 0, 0, 0.1);
  background-blend-mode: color;
  align-items: center;
  color: lightgrey !important;
  height: inherit;
  justify-content: center;
  margin: 0;
  padding: 1rem;
  text-align: center;
  border: 1px solid rgba(0, 0, 0, 0.25);
  border-radius: 1rem;
  width: 300px;
}

.description {
  font-size: 0.9rem;
  font-weight: 500;
}

.location {
  font-weight: 500;
}

.temp {
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}

.temp:hover {
  color: white;
}

.minmaxTemp span {
  cursor: pointer;
  font-weight: 400;
  margin: 0 1rem;
  transition: all 0.25s ease-in-out;
}

.minmaxTemp span:hover {
  color: white;
}

.weather-icon {
  margin-bottom: 0.5rem;
  margin-top: 1rem;
}

.weather-icon img {
  background: rgba(255, 255, 255, 0.5);
  border-radius: 1rem;
  box-shadow: 0.25rem 0.25rem 0.35rem rgba(0, 0, 0, 0.1);
  height: 100%;
  width: auto;
}

.loader-container {
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  pointer-events: none;
}

